<template>
  <div>
    <div v-if="loading" class="container-loader"><div class="loader">Cargando...</div></div>
    <b-navbar  variant="dark" align="center">
      <b-navbar-brand href="/recarga"><b-icon icon="arrow-left"></b-icon></b-navbar-brand>
      <h6>Recargar</h6>
      <router-link class="h5 ml-auto" to="/perfil" style="color:#fff">
        <b-icon class="mr-2" icon="person" />Mi perfil
      </router-link>
    </b-navbar>
      <b-container class="py-5">
        <div class="card card_pago">
          <b-alert show variant="danger" v-if="error_msg">{{error.general}}</b-alert>
          <img class="logo" src="@/assets/img/logo-2.png" />
          <div class="credit_cards">
            <img v-bind:style=" c_master ? 'opacity:1;' : 'opacity:0.5;'" src="@/assets/img/credit_master.png" />
            <img v-bind:style=" c_visa ? 'opacity:1;' : 'opacity:0.5;'" src="@/assets/img/credit_visa.png" />
          </div>
          <b-form-group>
            <b-input placeholder="Numero de Tarjeta" type="number" v-model="num_card" @change="pintarCard()"></b-input>
            <div class="label-error" v-if="error.num_card">{{error.num_card}}</div>
          </b-form-group>
          <b-form-row>
            <b-col>
              <b-form-group>
                <b-input placeholder="MM/AAAA"  pattern="'(1[0-2]|0[1-9])\/(1[5-9]|2\d)'" data-valid-example="05/18" v-model="fecha"></b-input>
                <div class="label-error" v-if="error.fecha">{{error.fecha}}</div>
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group>
                <b-input placeholder="CVV" v-model="cvv"></b-input>
                <div class="label-error" v-if="error.cvv">{{error.cvv}}</div>
              </b-form-group>
            </b-col>
          </b-form-row>
          <b-form-checkbox
            size="sm"
            v-model="checkbox"
          >
            He ledo y acepto los <b-link v-b-modal.modal-terminos>Trminos y Condiciones</b-link>
          </b-form-checkbox>
          <div class="label-error" v-if="error.checkbox">{{error.checkbox}}</div>
          <b-button block variant="primary" @click="RealizarPago()">PAGAR S/.{{monto}}</b-button>
        </div>
      </b-container>
      <b-modal id="modal-terminos" scrollable  title="Trminos y Condiciones">
        <p>1. DERECHOS DEL USUARIO DE ESTE SITIO <br/> La sola visita a este sitio en el cual se ofrecen diferentes modalidades de pago no impone al usuario obligacin alguna, a menos que haya aceptado las condiciones ofrecidas por Metropolitano, en la forma indicada en estos trminos y condiciones.
          El usuario goza de todos los derechos establecidos segn la legislacin vigente en el Per sobre proteccin al consumidor.
          Metropolitano efecta permanentemente todos los esfuerzos por asegurar la disponibilidad de esta pasarela de pagos, las 24 horas, los siete das de la semana, sin interrupciones. Sin embargo, y debido a la naturaleza misma del internet, a travs del cual opera este servicio, no es posible garantizar al 100% tal extremo.
          Asimismo, el acceso a esta pasarela de pagos podra ocasionalmente verse suspendido debido a la realizacin de trabajos mantenimiento o actualizacin del servicio con nuevas funcionalidades que tengan por objetivo brindarle un mejor servicio. Al respecto, procuraremos reducir en lo posible la frecuencia y duracin de tales suspensiones.
        </p>
        <p>
          2. MEDIOS DE PAGO QUE SE PODR UTILIZAR <br/>Los pagos a travs de esta pasarela podrn ser efectuados bajo una modalidad:
          a) Tarjetas de crdito y dbito Visa.
          El uso, condiciones de pago y otras condiciones aplicables a las tarjetas de crdito, son de exclusiva responsabilidad del emisor de su tarjeta.
          Dependiendo del nivel de riesgo, producto de la calificacin dada a la operacin por parte de los procesadores de pago, se solicitar al titular de la tarjeta de crdito/dbito confirmar la operacin autenticndose en Verified by Visa, por lo que previamente deber estar afiliado a este sistema de autentificacin en lnea. De no encontrarse afiliado, deber consultar con su Banco sobre el procedimiento de afiliacin a Verified by Visa.
          Los montos de pago aceptados por el usuario sern retenidos por un tiempo mximo de 04 das para su atencin. De no realizarse la transaccin de manera correcta y ser interrumpida esta antes de que el usuario pueda recibir el voucher electrnico de pago (por time out), o exceder el tiempo establecido, la retencin se libera y el pago del servicio queda anulado automticamente y sin cargo alguno.
          Metropolitano procesa los pagos va procesadores de pago locales, aplicando el cobro a su tarjeta de dbito/crdito en moneda local. Sin embargo, si usted utiliza una tarjeta de crdito/dbito emitida en el extranjero, el emisor de esta podra cargar el importe del pago en dlares norteamericanos, utilizando una tasa de cambio que fije el banco internacional de forma unilateral y en correspondencia a las condiciones de uso que tenga acordada para su tarjeta con dicha entidad.
        </p>
        <p>
          3. DERECHO DE ANULACIN <br/>
          Por ser un servicio en Lnea no se aceptan anulaciones. La transaccin se procesar inmediatamente recibida la conformidad del pago por parte del Banco Emisor de su tarjeta de crdito/dbito. Por tratarse de operaciones seguras, los cargos autorizados no son reembolsables.
        </p>
        <p>4. DELIMITACIN DE RESPONSABILIDAD <br/>
          Metropolitano, no se responsabiliza frente a los usuarios o terceros por los daos y perjuicios que sean consecuencia directa o indirecta de la interrupcin, suspensin o finalizacin de los servicios ofrecidos a travs de esta pasarela de pagos, siendo esto responsabilidad del emisor de las tarjetas.
          Metropolitano, no se responsabiliza por los errores del usuario en el registro de los datos requeridos para el pago de los servicios en esta ventanilla virtual. Es responsabilidad del usuario verificar toda la informacin registrada antes de proceder con el pago.</p>
      </b-modal>
      <b-modal id="modal-voucher" :no-close-on-backdrop="true" centered  title="Boleta de pago" hide-footer>
        <div class="text-center mb-4">
          <img class="logo" src="@/assets/img/logo-2.png" />
          <h3>Pago realizado</h3>
        </div>
        <p><strong>N de recibo</strong><span>{{voucher.nro_recibo}}</span></p>
        <p><strong>Tipo tarjeta</strong><span>{{getTipoTarjeta(voucher.tipo_tarjeta)}}</span></p>
        <p><strong>Tipo de transaccin</strong><span>{{getTipoTransaccion(voucher.tipo_transac)}}</span></p>
        <p><strong>Fecha</strong><span>{{voucher.fecha}}</span></p>
        <p><strong>N de tarjeta</strong><span>{{voucher.tarjeta_enmascarada}}</span></p>
        <p><strong>Recarga</strong><span>S/.{{voucher.recarga}}</span></p>
        <p><strong>Monto</strong><span>S/.{{voucher.monto}}</span></p>
        <p><strong>Cliente</strong><span>{{voucher.nombre}} {{voucher.apellido}}</span></p>
        <b-button class="mt-4" block variant="success" @click="goToHome()">REGRESAR</b-button>
      </b-modal>
  </div>
</template>

<script>
import axios from "axios";
  
  export default {
    data() {
      return {
        loading: false,
        codigo: "",
        error: {
          num_card:'',
          fecha: '',
          cvv: '',
          checkbox: '',
          general: ''
        },
        error_msg: false,
        modalMsg: '',
        monto: null,
        num_card: null,
        fecha: null,
        cvv: null,
        checkbox: false,
        c_visa: false,
        c_master: false,
        tipo: null,
        mes: null,
        anio: null,
        voucher: {}
      }
    },
    created() {
    },
    mounted() {
      this.monto = this.$route.params.monto;
      this.tipo = this.$route.params.tipo;
      
    },
    methods: {
      pintarCard(){
        let visa = /^(?:4[0-9]{12}(?:[0-9]{3})?)$/;
        let master = /^(?:5[1-5][0-9]{14})$/;
        if(this.num_card.match(visa)) {
          this.c_visa = true;
          console.log('VISA');
          this.error.num_card= '';
        } else {
          this.c_visa = false;
        }
        if(this.num_card.match(master)) {
          this.c_master = true;
          console.log('MASTER')
          this.error.num_card= '';
        } else {
          this.c_master = false;
        }
      },
      validateCampos() {
        this.loading = true;
        let visa = /^(?:4[0-9]{12}(?:[0-9]{3})?)$/;
        let master = /^(?:5[1-5][0-9]{14})$/;
        if(this.num_card) {
          if(this.num_card.match(visa) || this.num_card.match(master)) {
            this.loading = false;
            this.error.num_card = ''
            console.log('entro')
          }
          else {
            this.loading = false;
            this.error.num_card = 'El nmero ingresado es incorrecto'
            console.log('ERROR')
          }
        } else {
          this.loading = false;
          this.error.num_card = 'Debe ingresar el campo obligatorio'
        }
        if(this.fecha) {
          this.loading = false;
          let date = this.fecha;

          let dataArray = date.split('/');
          this.mes = dataArray[0];
          this.anio = dataArray[1];
          this.error.fecha = ''
        } else {
          this.loading = false;
          this.error.fecha = 'Debe ingresar fecha'
        }
        if(this.cvv) {
          this.error.cvv = '';
          this.loading = false;
        } else {
          this.loading = false;
          this.error.cvv = 'Debe ingresar cvv'
        }
      },
      RealizarPago() {
        this.validateCampos();
        this.loading = true;
        if(this.checkbox) {
          this.error.checkbox = ''
          let object = {
            "dni": this.dni,
            "nro": this.num_card,
            "mes" : this.mes,
            "anio" : this.anio,
            "ccv" : this.cvv,
            "tipo" : this.tipo,
            "monto" : this.monto
          };
          axios.post('http://metropolitano.atspace.cc/php/pasarela.php', object)
          .then(res => {
            let data = res.data;
            console.log("res : ",data)
            if(data.status == 200) {
              this.loading = false;
              this.error_msg = false;
              this.voucher = data.data;
              // this.getTipoTarjeta(data.data.tipo_tarjeta)
              this.$bvModal.show('modal-voucher')
              // this.showMsgModal(data.mensaje);
              // let data = res.data.data.data;
            }
            else {
              this.loading = false;
              
              this.error_msg = true;
              this.error.general = data.mensaje;
            }
          })
          .catch(err =>
            {
              console.log(err.response)
              this.loading = false;
              // this.error = true;
              // this.error_msg = err.response.data.data.cambio
            }
          )
        } else {
          this.loading = false;
          this.error.checkbox = 'Debe aceptar los trminos y Condiciones'
        }
      },
      showMsgModal(text) {
        this.modalMsg = '';
        this.$bvModal.msgBoxOk(text,{
          title: 'Mensaje',
        })
          .then(value => {
            console.log('value',value)
            // this.$router.push({
            //   name: 'recarga'
            // })
          })
      },
      goToHome() {
        this.$router.push({
          name: 'tarjetas'
        })
      },
      getTipoTarjeta(tipo) {
        switch(tipo) {
          case '0':
            return 'General'
          case '1':
            return 'Discapacitado'
          case '2':
            return 'Universitario'
          case '3':
            return 'Escolar'
          case '4':
            return 'Personalizada'
          default:
            return '--'
        }
      },
      getTipoTransaccion(tipo) {
        switch(tipo) {
          case '0':
            return 'Recarga'
          case '1':
            return 'Descuento'
          default:
            return '--'
        }
      }
    },
    computed: {
      dni: function(){
        return this.$store.state.user != null ? this.$store.state.user.data.dni : '--';
      },
    }
  }
</script>

